.btn {

  box-shadow: inset 0px 1px 0px 0px #a4e271;
  background: #89c403 linear-gradient(to bottom, #89c403 5%, #77a809 100%);
  border-radius: 6px;
  border: 1px solid #74b807;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-size: 15px;
  font-weight: bold;
  padding: 5px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #528009;
  outline: none;
  margin: 0 5px;

  &:hover {
    background: #77a809 linear-gradient(to bottom, #77a809 5%, #89c403 100%);
  }

  &:active {
    position: relative;
    top: 1px;
  }

}

.error{
  background: #c40303 linear-gradient(to bottom, #c40303 5%, #a80909 100%);
  box-shadow: inset 0px 1px 0px 0px #e27171;
  border: 1px solid #b80707;
  &:hover {
    background: #a80909 linear-gradient(to bottom, #a80909 5%, #c40303 100%);
  }
}



